// Now import React and testing utilities
import React from 'react';
import { render, screen } from '@testing-library/react';
import { useSettings } from '@/contexts/SettingsContext';

// Import the component AFTER all mocks are set up
import SettingsPage from '../page';

// Get the mocked function for testing
const mockUseSettings = useSettings as jest.MockedFunction<typeof useSettings>;

describe('SettingsPage - Isolated Test', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  test('renders settings page successfully', () => {
    render(<SettingsPage />);
    
    // Check if the mock was called
    expect(mockUseSettings).toHaveBeenCalled();
    
    // Check if the main heading is rendered
    expect(screen.getByText('Settings')).toBeInTheDocument();
    
    // Check if the description is rendered
    expect(screen.getByText('Manage your account preferences and integrations')).toBeInTheDocument();
  });

  test('displays theme selector', () => {
    render(<SettingsPage />);
    expect(screen.getByTestId('theme-selector')).toBeInTheDocument();
  });

  test('displays notification toggles', () => {
    render(<SettingsPage />);
    expect(screen.getByText('Email Notifications')).toBeInTheDocument();
  });
}); 